
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
body{
  background-color: #111;
  
}
.clearfix:after{
  content: '';
  display: block;
  clear: both;
}
header {
  position: relative;
  background-image: url(/ownsite/static/media/header.61e3a393.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  min-height: 80vh;
  text-align: center;
}

header h1 {
  position: absolute;
  width: 100%;
  top: 70%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #fff;
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  letter-spacing: 3px;
  padding: 20px 0;
  font-size: 60px;
  background-color: rgba(0, 0, 0, 0.5);
}

@media (max-width: 860px) {
  header h1 {
    font-size: 50px;
  }
}

@media (max-width: 375px) {
  header h1 {
    font-size: 40px;
  }
}
@media (max-width: 425px) {
  header h1 {
    font-size: 40px;
    letter-spacing: 1px;
  }
}

nav {
  position: fixed;
  top: 0;
  right: 50%;
  -webkit-transform: translate(50%);
          transform: translate(50%);
  width: 100%;
  height: 70px;
  z-index: 1;
}
nav.blackBgc {
  background-color: #000;
}

ul {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
  width: 70%;
  height: 100%;
}

li {
  position: relative;
  list-style: none;
  overflow: hidden;
  color: white;
  font-size: 20px;
  text-transform: uppercase;
  font-family: roboto, sans-serif;
  border: 1px solid white;
  padding: 10px 10px;
  border-radius: 10px;
  cursor: pointer;
}
nav.blackBgc ul li {
  border: none;
}

li:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2%;
  top: 50%;
  right: 100%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  transition: height 0.1s 0.1s;
  background-color: #fff;
  opacity: 0.7;
}

li:hover:before {
  left: 0%;
  height: 100%;
  border-radius: 10px;
  z-index: -1;
  opacity: 0.7;
}
li:hover {
  color: black;
}
a {
  color: white;
}
.fas.fa-times {
  display: none;
}
.fa-home,
.fa-project-diagram,
.fa-user-tie,
.fa-address-card {
  display: inline-block;
  padding: 5px 20px 5px 5px;
}

@media (max-width: 909px) {
  nav {
    position: fixed;
    height: 0;
    transition: 0.6s;
  }
  nav.active {
    position: fixed;
    width: 100%;
    height: 300px;
    opacity: 0.9;
    background-color: #000;
  }

  ul {
    display: none;
    transition: 0.6s;
  }
  ul.active {
    display: block;
  }
  ul li.active {
    margin: 20px 0;
    text-align: center;
    left: 0;
    opacity: 1;
  }

  .fas.fa-times {
    position: fixed;
    display: block;
    top: 10px;
    right: 10px;
    font-size: 40px;
    color: #ddd;
  }
  .fas.fa-times.active {
    color: #ddd;
  }
}

main {
  overflow-x: hidden;
}
main p {
  font-size: 30px;
  padding: 0 40px;
  font-family: Lato, sans-serif;
  color: white;
  letter-spacing: 1px;
  line-height: 50px;
  text-align: center;
}
main i {
  padding-right: 10px;
}
h2 {
  width: 100%;
  font-size: 50px;
  line-height: 90px;
  margin-bottom: 30px;
  background-color: #222;
  color: white;
  font-family: roboto, sans-serif;
  text-align: center;
}
section {
  max-width: 1400px;
  margin: 0 auto;
}
.wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.wrap div {
  width: 50%;
  text-align: center;
}
.wrap .wrapLeft img {
  width: 150px;
  height: 150px;
  margin: 20px 0;
  transition: 1s;
  -webkit-transform: translateX(-100vw);
          transform: translateX(-100vw);
}
.wrap .wrapLeft img:nth-of-type(1) {
  transition: 1s 0.3s;
}
.wrap .wrapLeft img:nth-of-type(2) {
  transition: 1s 0.2s;
}
.wrap .wrapLeft img:nth-of-type(3) {
  transition: 1s 0.1s;
}
.wrap .wrapLeft img.scroll {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.wrap .wrapRight img {
  display: inline-block;
  width: 400px;
  margin-top: 20px;
  -webkit-transform: translateX(100vw);
          transform: translateX(100vw);
  transition: 1s;
}
.wrap .wrapRight img.scroll {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.project {
  text-align: center;
  margin: 0 auto 30px;
}
.project h3 {
  text-align: center;
  font-size: 40px;
  padding-bottom: 30px;
  color: white;
  font-family: roboto, sans-serif;
}
.project p {
  float: left;
  width: 50%;
}
.project img {
  width: 40%;
}
.about .pictures {
  position: relative;
  height: 400px;
  width: 100%;
  margin-bottom: 30px;
}
.about .pictures .item {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  overflow: hidden;
  top: 0;
  left: 15%;
  width: 70%;
  height: 98%;
}
.about .pictures .item img {
  display: block;
  width: 50%;
  height: 90%;
  opacity: 0;
  -webkit-animation: image 16s linear infinite;
          animation: image 16s linear infinite;
}

.about .pictures .item:nth-of-type(2) img {
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
.about .pictures .item:nth-of-type(3) img {
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
}
.about .pictures .item:nth-of-type(4) img {
  -webkit-animation-delay: 12s;
          animation-delay: 12s;
}
.about .pictures .item p {
  -webkit-transform: translateX(130%);
          transform: translateX(130%);
  width: 40%;
  -webkit-animation: text 16s linear infinite;
          animation: text 16s linear infinite;
}
.about .pictures .item:nth-of-type(2) p {
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
.about .pictures .item:nth-of-type(3) p {
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
}
.about .pictures .item:nth-of-type(4) p {
  -webkit-animation-delay: 12s;
          animation-delay: 12s;
}

.cooperation {
  margin-bottom: 30px;
}
.contact {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.contact p {
  -webkit-flex-basis: 49%;
          flex-basis: 49%;
  color: #fff;
  margin: 10px 4px;
}
.contact a {
  text-decoration: none;
}

.contact p:hover {
  cursor: pointer;
  color: #999;
}
@-webkit-keyframes image {
  0% {
    opacity: 0;
  }
  3% {
    opacity: 1;
  }
  20% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
@keyframes image {
  0% {
    opacity: 0;
  }
  3% {
    opacity: 1;
  }
  20% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes text {
  0% {
    -webkit-transform: translateX(110%);
            transform: translateX(110%);
  }
  3% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  20% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  25% {
    -webkit-transform: translateX(110%);
            transform: translateX(110%);
  }
}
@keyframes text {
  0% {
    -webkit-transform: translateX(110%);
            transform: translateX(110%);
  }
  3% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  20% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  25% {
    -webkit-transform: translateX(110%);
            transform: translateX(110%);
  }
}
@media (max-width: 1400px) {
  main p {
    font-size: 27px;
    padding: 0 20px;
  }
  .wrap {
    -webkit-justify-content: center;
            justify-content: center;
  }
  .wrap div {
    width: 80%;
    text-align: center;
    margin-bottom: 20px;
  }
  .project h3 {
    font-size: 38px;
    line-height: 50px;
  }
}

@media (max-width: 1024px) {
  main p {
    font-size: 24px;
    padding: 0 20px;
    color: white;
    line-height: 35px;
    text-align: center;
  }
  .wrap .wrapLeft img {
    width: 100px;
    height: 100px;
    margin: 20px 0;
  }
  .contact p {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    margin: 10px auto;
  }
}

@media (max-width: 768px) {
  .project p {
    width: 100%;
  }
  .about .pictures {
    height: 250px;
  }
}
@media (max-width: 425px) {
  h2 {
    width: 100%;
    font-size: 35px;
    line-height: 50px;
    margin-bottom: 10px;
  }
  main p {
    font-size: 20px;
    padding: 10px;
    line-height: 30px;
  }
  .wrap div {
    width: 100%;
    padding: 0 20px;
    margin-bottom: 20px;
  }
  .wrap .wrapLeft img {
    width: 100px;
    height: 100px;
    margin: 10px 20px;
  }
  .wrap .wrapRight img {
    display: inline-block;
    width: 280px;
    margin-top: 20px;
  }
  .project h3 {
    font-size: 30px;
    line-height: 30px;
    margin-top: 20px;
    padding-bottom: 10px;
  }
  .project img {
    width: 80%;
  }
  .about .pictures {
    margin-top: 10px;
    height: 20vh;
  }
  .about .pictures .item {
    width: 90%;
    left: 5%;
    height: 100%;
  }
  .about .pictures .item p {
    font-size: 18px;
    line-height: 18px;
  }
  .contact p {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    font-size: 16px;
    margin: 1px auto;
  }
}

.footer{
  color: white;
  font-size: 20px;
  line-height: 40px;
  margin-top: 20px;
  background-color: #222;
  text-align: center;
  font-family: roboto;
  letter-spacing: 1px;
}
